import React, { useState, useEffect, useContext } from "react";
import { Dropdown } from "react-bootstrap";
import { Link } from "react-router-dom";
import { ThemeContext } from "../../../context/ThemeContext";
import LogoutPage from './Logout';
import { HiOutlineLanguage } from "react-icons/hi2";
import { IMAGES, SVGICON } from "../../constant/theme";
import Logoutbtn from "./Logoutbtn";

const NotificationBlog = ({ classChange }) => {
	return (
		<>
			<li>
				<div className="timeline-panel">
					<div className="media me-2">
						<img alt="images" width={50} src={IMAGES.Avatar} />
					</div>
					<div className="media-body">
						<h6 className="mb-1">Dr sultads Send you Photo</h6>
						<small className="d-block">29 July 2022 - 02:26 PM</small>
					</div>
				</div>
			</li>
			<li>
				<div className="timeline-panel">
					<div className={`media me-2 ${classChange}`}>KG</div>
					<div className="media-body">
						<h6 className="mb-1">Resport created successfully</h6>
						<small className="d-block">29 July 2022 - 02:26 PM</small>
					</div>
				</div>
			</li>
			<li>
				<div className="timeline-panel">
					<div className={`media me-2 ${classChange}`}><i className="fa fa-home" /></div>
					<div className="media-body">
						<h6 className="mb-1">Reminder : Treatment Time!</h6>
						<small className="d-block">29 July 2022 - 02:26 PM</small>
					</div>
				</div>
			</li>
		</>
	)
}

const Header = ({ onNote, role }) => {
	const [headerFix, setheaderFix] = useState(false);
	useEffect(() => {
		window.addEventListener("scroll", () => {
			setheaderFix(window.scrollY > 50);
		});
	}, []);

	const { lang, setLang } = useContext(ThemeContext);
	return (
		<div className={`header ${headerFix ? "is-fixed" : ""}`} >
			<div className="header-content" >
				<nav className="navbar navbar-expand">
					<div className="collapse navbar-collapse justify-content-right" >

						<ul className="ml-auto navbar-nav header-right">

							<Dropdown as="li" className="nav-item dropdown language_dropdown">
								<Dropdown.Toggle className="nav-link i-false  c-pointer" as="a">
									<HiOutlineLanguage />
								</Dropdown.Toggle>
								<Dropdown.Menu align="end" className="mt-2 dropdown-menu dropdown-menu-end">
									<Link className="dropdown-item" to="#" onClick={() => setLang('ar')}>
										Arabic
									</Link>
									<Link className="dropdown-item" to="#" onClick={() => setLang('en')}>
										English
									</Link>
								</Dropdown.Menu>
							</Dropdown>




							<li className="nav-item align-items-center header-border">
								<Logoutbtn />
							</li>
							<li className="nav-item ps-3">
								<Dropdown className="header-profile2">
									<Dropdown.Toggle className="nav-link i-false" as="div">
										<div className="header-info2 d-flex align-items-center">
											<div className="header-media">
												<img src={IMAGES.Tab1} alt="" />
											</div>
											<div className="header-info">
												<h6>Thomas Fleming ({role})</h6>
												<p>info@gmail.com</p>
											</div>

										</div>
									</Dropdown.Toggle>
									<Dropdown.Menu align="end">
										<div className="card border-0 mb-0">
											<div className="card-header py-2">
												<div className="products">
													<img src={IMAGES.Tab1} className="avatar avatar-md" alt="" />
													<div>
														<h6>Thomas Fleming</h6>
														<span>Web Designer</span>
													</div>
												</div>
											</div>
											<div className="card-body px-0 py-2">
												<Link to={"/app-profile"} className="dropdown-item ai-icon ">
													{SVGICON.User}{" "}
													<span className="ms-2">Profile </span>
												</Link>

												<Link to={"/changepassword"} className="dropdown-item ai-icon ">
													{SVGICON.Password} {" "}
													<span className="ms-2">Change Password </span>
												</Link>
											</div>
											<div className="card-footer px-0 py-2">
												<LogoutPage />
											</div>
										</div>

									</Dropdown.Menu>
								</Dropdown>
							</li>
						</ul>


					</div>
				</nav>
			</div>
		</div>
	);
};

export default Header;
